import React from 'react'
import moment from 'moment'
import { FaRegPaperPlane, FaSpaceShuttle, FaSun } from 'react-icons/fa'

export const TaskDate = ({ setTaskDate, showTaskDate, setShowTaskDate }) => 
showTaskDate && (
  <div className="task-date" data-testid="task-date-overlay">
    <ul className="task-date_list">
      <li>
        <div
          data-testid="task-date-today"
          onClick={() => {
            setShowTaskDate(false)
            setTaskDate(moment().format('DD/MM/YYYY'))
          }}
           onKeyDown={(e) => {
             if(e.key === 'Enter') {
              setShowTaskDate(false)
              setTaskDate(moment().format('DD/MM/YYYY'))
             }
          }}
          tabIndex={0}
          role="button"
          aria-label="Select today as the task date"
        >
          <span><FaSpaceShuttle /></span>
          <span>Today</span>
        </div>
      </li>
      <li>
        <div
          data-testid="task-date-tomorrow"
          onClick={() => {
            setShowTaskDate(false)
            setTaskDate(moment().add(1, 'day').format('DD/MM/YYYY'))
          }}
          onKeyDown={(e) => {
            if(e.key === 'Enter') {
              setShowTaskDate(false)
              setTaskDate(moment().add(1, 'day').format('DD/MM/YYYY'))
            }
          }}
          role="button"
          tabIndex={0}
          aria-label="Select tomorrow as the task date"
        >
          <span><FaSun /></span>
          <span>Tomorrow</span>
        </div>
      </li>
      <li>
        <div
          data-testid="task-date-next_week"
          onClick={() => {
            setShowTaskDate(false)
            setTaskDate(moment().add(7, 'day').format('DD/MM/YYYY'))
          }}
          onKeyDown={(e) => {
            if(e.key === 'Enter') {
              setShowTaskDate(false)
              setTaskDate(moment().add(7, 'day').format('DD/MM/YYYY'))
            }
          }}
          role="button"
          tabIndex={0}
          aria-label="Select next week as the task date"
        >
          <span><FaRegPaperPlane /></span>
          <span>Next week</span>
        </div>
      </li>
    </ul>
  </div>
)
